.container {
    width: 90%;
    max-width: 1200px;
    margin: 0 auto;
    background-color: rgb(73, 73, 73);
    color: aliceblue;
    padding-bottom: 2rem;
}

.tit {
    text-align: center;
    font-family: algerian;
    font-size: 37px;
    padding: 5px;
}

.todos {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    font-size: 23px;
}

.todos p {
    margin: 0;
    color: black;
    padding: 10px;
}

.todo button {
    margin: 10px;
}

.todo {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 50%;
    background-color: rgb(184, 183, 183);
    height: 60px;
    border-radius: 6px;
    transition: all 0.2s;
    padding: 0 10px;
}

.todo:hover {
    transform: scale(1.02);
}

.btn {
    border: none;
    line-height: 0;
    font-size: 2rem;
    cursor: pointer;
    transition: all 0.2s;
    padding: 0;
}

.btn:hover {
    transform: scale(1.3);
}

.forms {
    width: 50%;
    margin: 0 auto;
    padding: 27px;
    display: flex;
    text-align: center;
}

.inform {
    width: 79%;
    display: flex;
    text-align: center;
    height: 40px;
    border-radius: 5px 0px 0px 5px;
    font-size: larger;
    font-weight: bolder;
    border: none;
}

.btn2 {
    height: 42px;
    border-radius: 0px 5px 5px 0px;
    font-size: larger;
    font-weight: bolder;
    border: none;
    color: white;
    background-color: blueviolet;
}

.btn3 {
    border: none;
    line-height: 0;
    font-size: 1.7rem;
    cursor: pointer;
    transform: translateY(-5.7px);
    transition: all 0.2s;
    padding: 0;
}

.btn3:hover {
    transform: scale(1.3);
}

/* Small devices (phones, 600px and down) */
@media (max-width: 600px) {
    .container {
        width: 90%;
        padding: 1rem;
    }

    .tit {
        font-size: 24px;
    }

    .todo {
        width: 100%;
        height: auto;
        flex-direction: row;
        align-items: center;
        padding: 0.5rem;
    }

    .btn {
        font-size: 1.5rem;
    }

    .forms {
        padding: 1rem;
        flex-direction: column;
        
    }

    .inform {
        width: 225px;
        margin-bottom: 0.5rem;
        border-radius: 5px;
    }

    .btn2 {
        
        border-radius: 5px;
    }
}

